@import url(https://fonts.googleapis.com/css?family=Rubik:400,700&display=swap);
body{margin:0;padding:0;background:#37474f;font-family:'Rubik', sans-serif;color:#ffffff}

.container{display:grid;border:2px solid #2f3d44;border-radius:5px;background:#263238;margin:2rem auto;width:350px;height:60vh;max-height:700px;font-size:1.4rem;text-align:center}.container #display{display:grid;width:calc(100%-0.3rem);height:calc(90%-0.3rem);background:#071016;border:2px solid #2f3d44;border-radius:4px;padding:0.3rem;margin:0.5rem 0.5rem 0 0.5rem;align-items:center;color:#e65100}.container .volume-slider{display:grid;align-items:center;grid-template-columns:0.5fr 2fr 0.5fr;grid-template-rows:1fr;font-size:0.625rem;margin:0 1rem}.container .pads-pane{display:grid;grid-template-columns:repeat(3, 1fr);grid-template-rows:repeat(3, 1fr);grid-gap:0.05rem;gap:0.05rem;margin:0 0.4rem}.container .pads-pane .drum-pad{display:flex;height:70%;background:#071016;background-position:center;-webkit-transition:background 0.8s;transition:background 0.8s;box-shadow:0 0 2px #807f7f;border-radius:4px;padding:0.4rem 0.8rem;margin:0 0.2rem;justify-content:center;align-items:center;cursor:pointer}.container .pads-pane .drum-pad:active{background-color:#e65100;background-size:100%;-webkit-transition:background 0s;transition:background 0s}

